	.bg1
		background-image: url('../img/bg1.jpg')
		background-size: cover
		background-repeat: no-repeat
		padding-top: 3.75rem 
	.bg2
		background-image: url('../img/bg2.jpg')
		background-size: cover
		background-repeat: no-repeat
		padding-top: 3.75rem 
	.bg3
		background-image: url('../img/bg3.jpg')
		background-size: cover
		background-repeat: no-repeat
		padding-top: 3.75rem 
	.bg4
		background-image: url('../img/bg4.jpg')
		background-size: cover
		background-repeat: no-repeat
		padding-top: 3.75rem 
	.bg5,.bg6,.bg7,.bg8,.bg9,.bg10,.bg11
		background-image: url('../img/bg1.jpg')
		background-size: cover
		background-repeat: no-repeat
		padding-top: 3.75rem 
	.product-list1-nav,.bg1,.bg2,.bg3,.bg4,.bg5,.bg6,.bg7,.bg8,.bg9,.bg10,.bg11
		position: relative
		&:after
			content: ''
			display: block
			width: 100%
			height: 100%
			position: absolute
			top: 0
			left: 0
			background: rgba(0,0,0,0.6)
.product-wrapper
	padding: 0 30px
	position: relative
	z-index: 99
	.title-h1-grey
		margin-left: 30px
		color: #ffffff
	.box-top
		margin-bottom: 2rem
		.content 
			p 
				font-size: 18px
				font-weight: 500
				color: #ffffff
		@media (max-width: 1025px)
			.content
				padding-bottom: 2rem
		nav
			a 
				display: flex
				white-space: nowrap
				position: relative
				flex: 0 0 50%
				transition: all .3s ease-in-out
				margin-bottom: 24px
				padding-left: 30px
				max-width: 50%
				color: #999999
				font-size: 18px!important
				font-weight: 600
				text-transform: uppercase
				&:before 
					position: absolute
					width: 15px
					height: 15px
					background: #ffffff
					content: ''
					left: 0 
					top: 50%
					transform: translateY(-50%)
					display: block
					opacity: 0
					transition: all .4s ease-in-out
				&:hover,&.active
					position: relative
					color: #ffffff
					&:before
						opacity: 1
		@media (max-width: 1025px)
			nav 
				margin: 0
	@media (max-width: 1025px)
		.box-top
			padding: 0 15px